}

.topnav {
  overflow: hidden;
  background-color: #fff;
  margin: 10px;
  padding: 10px;
  border: 2px solid #000;
}

.topnav a {
  float: left;
  font-family: Roboto;
  font-style: normal;
  font-weight: 600;
  font-size: 20px;
  line-height: 30px;
  color: #3f51b5;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
}

.topnav a:hover {
  color: #021ba5;
}

.hover-underline-animation {
  display: inline-block;
  position: relative;
  color: #021ba5;
}

.hover-underline-animation:after {
  content: "";
  position: absolute;
  width: 99%;
  transform: scaleX(0);
  height: 2px;
  bottom: 0;
  left: 0;
  background-color: #021ba5;
  transform-origin: bottom right;
  transition: transform 0.25s ease-out;
}

.hover-underline-animation:hover:after {
  transform: scaleX(1);
  transform-origin: bottom left;
}

.topnav a:focus {
  color: #3753ee;
}

.topnav a.active {
  color: #3753ee;
}

.topnav-right {
  float: right;
}

@media only screen and (max-width: 834px) {
  .topnav a {
    float: left;
    font-size: 16px;
    line-height: 30px;
    padding: 14px 12px;
  }
  .logo {
    width: 20rem;
    /* height: 3rem; */
  }
}

@media only screen and (max-width: 768px) {
  .topnav a {
    float: left;
    font-size: 16px;
    line-height: 30px;
